#cal {
  font-size: 0.12rem;
  width:100%;
}

#cal #top {
  height: 0.40rem;
  line-height: 0.40rem;
  background: #429e6c;
  /*border: 1px solid #429e6c;*/
  color: #fff;
  padding: 0 0.10rem;
  clear: both;
}

#cal #top select {
  font-size: 0.12rem
}

#cal #top input {
  padding: 0
}

#cal ul#wk {
  margin: 0;
  padding: 0;
  height: 0.35rem;
  color: #888;
  font-size: 0.14rem;
  /*border-left: 1px solid #429e6c;*/
  /*border-right: 1px solid #429e6c;*/
  display: -webkit-flex;
}

#cal ul#wk li {
  width: 0.53rem;
  text-align: center;
  line-height: 0.25rem;
  list-style: none
}

#cal ul#wk li b {
  font-weight: normal;
  color: #429e6c;
}

#cal #cm {
  width:100%;
  clear: left;
  position: relative;
  /*border-left: 1px solid #429e6c;*/
  /*border-right: 1px solid #429e6c;*/
  display: -webkit-flex;
  -webkit-flex-wrap: wrap;
}

#cal #cm .cell {
  width: 0.53rem;
  height: 0.36rem;
  text-align: center;
  cursor: pointer;
}

#cal #cm .cell .so {
  font: 0.16rem arial;
}

#cal #bm {
  text-align: right;
  height: 0.30rem;
  line-height: 0.30rem;
  padding: 0 0.13rem 0 0;
  /*border: 1px solid #429e6c;*/
  /*border-top: 1px dashed #eee;*/
  clear: both;
  padding: 0 0.10rem;
  font-size: 0.14rem;
  display: -webkit-flex;
  -webkit-justify-content: space-between;
}

#cal #bm .heavenly-branch {
  color: #429e6c;
  display: -webkit-flex;
}

#cal #fd {
  display: none;
  position: absolute;
  border: 1px solid #dddddf;
  background: #fff;
  color: #333;
  padding: 0.10rem;
  line-height: 0.21rem;
  width: 1.50rem;
  border-radius: 0.10rem;
}

#cal #fd:before{
  content: '';
  position: absolute;
  left: -0.08rem;
  width: 0;
  height: 0;
  border-top: 0.08rem solid transparent;
  border-right: 0.08rem solid #dddddf;
  border-bottom: 0.08rem solid transparent;
}

#cal #fd:after{
  content: '';
  position: absolute;
  left: -0.06rem;
  top: 0.10rem;
  width: 0;
  height: 0;
  border-top: 0.08rem solid transparent;
  border-right: 0.08rem solid #fff;
  border-bottom: 0.08rem solid transparent;
}

#cal #fd b {
  font-weight: normal;
  color: #429e6c;
}

#cal .step {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  font-size: 0.16rem;
}

#cal .step span{
  cursor: pointer;
}

#cal .step #prev{
  margin-right: 0.10rem;
}

#cal .select{
  float: left;
}

#cal .select select{
  -webkit-appearance: none;
  padding: 0.05rem 0.20rem;
}

#cal #now-date{
  float: right;
  padding: 0px 0.10rem;
  background-color: #429e6c;
  color: #fff;
  font-size: 0.12rem;
  height: 0.20rem;
  line-height: 0.20rem;
  margin-top: 0.06rem;
  text-decoration: none;
}
